if (window.FC === undefined) { window.FC = {}; }

(function() {

  class QuizSummaryComponent extends React.Component {

    componentDidMount() {
      var ctx = this.canvas.getContext('2d');
      ctx.fillRect(0, 0, 50, 50);
    }

    render() {
      return <div className="quiz-summary-component">
        <h2>Summary</h2>

        <p className="correct">Correct: {this.props.correct}</p>
        <p className="incorrect">Incorrect: {this.props.incorrect}</p>
        <canvas ref={(dom) => { this.canvas = dom; }}></canvas>

      </div>
    }

  }

  FC.QuizSummaryComponent = QuizSummaryComponent;

})();
